namespace $ { export class $mol_suggest extends $mol_view {

	/// suggests /
	suggests() {
		return [] as any[]
	}

	/// eventPress?val null
	@ $mol_mem()
	eventPress( val? : any ) {
		return ( val !== void 0 ) ? val : <any> null
	}

	/// event * keydown?val <=> eventPress?val
	event() {
		return $mol_merge_dict( super.event() , {
			"keydown" : ( val? : any )=> <any> this.eventPress( val ) ,
		} )
	}

	/// selectedRow?val 0
	@ $mol_mem()
	selectedRow( val? : any ) {
		return ( val !== void 0 ) ? val : 0
	}

	/// focused false
	focused() {
		return false
	}

	/// suggest!id \
	suggest( id : any ) {
		return ""
	}

	/// eventRowerSelected!id?val null
	@ $mol_mem_key()
	eventRowerSelected( id : any , val? : any ) {
		return ( val !== void 0 ) ? val : <any> null
	}

	/// selected!id false
	selected( id : any ) {
		return false
	}

	/// rower!id $mol_suggest_rower 
	/// 	text <= suggest!id 
	/// 	prefix <= value 
	/// 	eventSelected?val <=> eventRowerSelected!id?val 
	/// 	selected <= selected!id
	@ $mol_mem_key()
	rower( id : any ) {
		return new $mol_suggest_rower().setup( obj => { 
			obj.text = () => this.suggest(id)
			obj.prefix = () => this.value()
			obj.eventSelected = ( val : any ) => this.eventRowerSelected(id , val )
			obj.selected = () => this.selected(id)
		} )
	}

	/// value?val \
	@ $mol_mem()
	value( val? : any ) {
		return ( val !== void 0 ) ? val : ""
	}

	/// hint \
	hint() {
		return ""
	}

	/// stringer $mol_string 
	/// 	value?val <=> value?val 
	/// 	hint <= hint
	@ $mol_mem()
	stringer() {
		return new $mol_string().setup( obj => { 
			obj.value = ( val : any ) => this.value( val )
			obj.hint = () => this.hint()
		} )
	}

	/// suggestrows /
	suggestrows() {
		return [] as any[]
	}

	/// lister $mol_list 
	/// 	minimal_height 0 
	/// 	sub <= suggestrows
	@ $mol_mem()
	lister() {
		return new $mol_list().setup( obj => { 
			obj.minimal_height = () => 0
			obj.sub = () => this.suggestrows()
		} )
	}

	/// sub / 
	/// 	<= stringer 
	/// 	<= lister
	sub() {
		return [].concat( this.stringer() , this.lister() )
	}

} }

namespace $ { export class $mol_suggest_rower extends $mol_button {

	/// dom_name \div
	dom_name() {
		return "div"
	}

	/// eventSelected?val null
	@ $mol_mem()
	eventSelected( val? : any ) {
		return ( val !== void 0 ) ? val : <any> null
	}

	/// event * mousedown?val <=> eventSelected?val
	event() {
		return $mol_merge_dict( super.event() , {
			"mousedown" : ( val? : any )=> <any> this.eventSelected( val ) ,
		} )
	}

	/// selected false
	selected() {
		return false
	}

	/// attr * mol_suggest_selected <= selected
	attr() {
		return $mol_merge_dict( super.attr() , {
			"mol_suggest_selected" : ()=> <any> this.selected() ,
		} )
	}

	/// minimal_height 36
	minimal_height() {
		return 36
	}

	/// text \
	text() {
		return ""
	}

	/// prefix \
	prefix() {
		return ""
	}

	/// dimmer $mol_dimmer 
	/// 	haystack <= text 
	/// 	needle <= prefix
	@ $mol_mem()
	dimmer() {
		return new $mol_dimmer().setup( obj => { 
			obj.haystack = () => this.text()
			obj.needle = () => this.prefix()
		} )
	}

	/// sub / <= dimmer
	sub() {
		return [].concat( this.dimmer() )
	}

} }

