namespace $ { export class $mol_perf_render extends $mol_view {

	/// title \$mol
	title() {
		return "$mol"
	}

	/// titler $mol_view 
	/// 	dom_name \h2
	/// 	sub / <= title
	@ $mol_mem()
	titler() {
		return new $mol_view().setup( obj => { 
			obj.dom_name = () => "h2"
			obj.sub = () => [].concat( this.title() )
		} )
	}

	/// runnerLabel @ \Run
	runnerLabel() {
		return $mol_locale.text( this.locale_contexts() , "runnerLabel" )
	}

	/// eventRun?val null
	@ $mol_mem()
	eventRun( val? : any ) {
		return ( val !== void 0 ) ? val : <any> null
	}

	/// runner $mol_button_major 
	/// 	dom_name \button
	/// 	sub / <= runnerLabel 
	/// 	event_click?val <=> eventRun?val
	@ $mol_mem()
	runner() {
		return new $mol_button_major().setup( obj => { 
			obj.dom_name = () => "button"
			obj.sub = () => [].concat( this.runnerLabel() )
			obj.event_click = ( val : any ) => this.eventRun( val )
		} )
	}

	/// head / 
	/// 	<= titler 
	/// 	<= runner
	head() {
		return [].concat( this.titler() , this.runner() )
	}

	/// header $mol_view sub / <= head
	@ $mol_mem()
	header() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => [].concat( this.head() )
		} )
	}

	/// rows /
	rows() {
		return [] as any[]
	}

	/// lister $mol_list rows <= rows
	@ $mol_mem()
	lister() {
		return new $mol_list().setup( obj => { 
			obj.rows = () => this.rows()
		} )
	}

	/// contenter $mol_scroll sub / <= lister
	@ $mol_mem()
	contenter() {
		return new $mol_scroll().setup( obj => { 
			obj.sub = () => [].concat( this.lister() )
		} )
	}

	/// sub / 
	/// 	<= header 
	/// 	<= contenter
	sub() {
		return [].concat( this.header() , this.contenter() )
	}

} }

namespace $ { export class $mol_perf_render_row extends $mol_view {

	/// selected?val false
	@ $mol_mem()
	selected( val? : any ) {
		return ( val !== void 0 ) ? val : false
	}

	/// minimal_height 24
	minimal_height() {
		return 24
	}

	/// attr * mol_perf_render_row_selected <= selected
	attr() {
		return $mol_merge_dict( super.attr() , {
			"mol_perf_render_row_selected" : ()=> <any> this.selected() ,
		} )
	}

	/// eventToggle?val null
	@ $mol_mem()
	eventToggle( val? : any ) {
		return ( val !== void 0 ) ? val : <any> null
	}

	/// event * click?val <=> eventToggle?val
	event() {
		return $mol_merge_dict( super.event() , {
			"click" : ( val? : any )=> <any> this.eventToggle( val ) ,
		} )
	}

	/// label \
	label() {
		return ""
	}

	/// bar $mol_view sub / <= label
	@ $mol_mem()
	bar() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => [].concat( this.label() )
		} )
	}

	/// sub / <= bar
	sub() {
		return [].concat( this.bar() )
	}

} }

