namespace $ { export class $mol_suggest_demo_base extends $mol_suggest {

	/// query_suggests /
	query_suggests() {
		return [] as any[]
	}

	/// suggests <= query_suggests
	suggests() {
		return this.query_suggests()
	}

	/// query?val \
	@ $mol_mem()
	query( val? : any ) {
		return ( val !== void 0 ) ? val : ""
	}

	/// value?val <=> query?val
	@ $mol_mem()
	value( val? : any ) {
		return this.query( val )
	}

} }

namespace $ { export class $mol_suggest_demo_empty extends $mol_suggest {

	/// focused false
	focused() {
		return false
	}

	/// value \
	value() {
		return ""
	}

	/// suggests /
	suggests() {
		return [] as any[]
	}

} }

namespace $ { export class $mol_suggest_demo_opened extends $mol_suggest {

	/// hint @ \Name
	hint() {
		return $mol_locale.text( this.locale_contexts() , "hint" )
	}

	/// value \Ivan
	value() {
		return "Ivan"
	}

	/// focused true
	focused() {
		return true
	}

	/// selected_row 2
	selected_row() {
		return 2
	}

	/// suggest1 @ \Evgeny Ivanov
	suggest1() {
		return $mol_locale.text( this.locale_contexts() , "suggest1" )
	}

	/// suggest2 @ \Alexander Ivanov
	suggest2() {
		return $mol_locale.text( this.locale_contexts() , "suggest2" )
	}

	/// suggest3 @ \Dmitriy Ivanov
	suggest3() {
		return $mol_locale.text( this.locale_contexts() , "suggest3" )
	}

	/// suggest4 @ \Yuriy Ivanov
	suggest4() {
		return $mol_locale.text( this.locale_contexts() , "suggest4" )
	}

	/// suggest5 @ \Nataliya Ivanov
	suggest5() {
		return $mol_locale.text( this.locale_contexts() , "suggest5" )
	}

	/// suggests / 
	/// 	<= suggest1 
	/// 	<= suggest2 
	/// 	<= suggest3 
	/// 	<= suggest4 
	/// 	<= suggest5
	suggests() {
		return [].concat( this.suggest1() , this.suggest2() , this.suggest3() , this.suggest4() , this.suggest5() )
	}

} }

