namespace $ { export class $mol_expander extends $mol_list {

	/// expanded?val false
	@ $mol_mem()
	expanded( val? : any ) {
		return ( val !== void 0 ) ? val : false
	}

	/// label /
	label() {
		return [] as any[]
	}

	/// labeler $mol_check_expand 
	/// 	checked?val <=> expanded?val 
	/// 	label <= label
	@ $mol_mem()
	labeler() {
		return new $mol_check_expand().setup( obj => { 
			obj.checked = ( val : any ) => this.expanded( val )
			obj.label = () => this.label()
		} )
	}

	/// content null
	content() {
		return <any> null
	}

	/// rows / 
	/// 	<= labeler 
	/// 	<= content
	rows() {
		return [].concat( this.labeler() , this.content() )
	}

} }

