namespace $ { export class $mol_expander_demo extends $mol_scroll {

	/// expander $mol_expander 
	/// 	label / \Lorem Ipsum
	/// 	content $mol_filler
	@ $mol_mem()
	expander() {
		return new $mol_expander().setup( obj => { 
			obj.label = () => [].concat( "Lorem Ipsum" )
			obj.content = () => new $mol_filler()
		} )
	}

	/// sub / <= expander
	sub() {
		return [].concat( this.expander() )
	}

} }

